body {
    line-height: 1.5;
    font-family: 'Poppins', sans-serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    max-width: 1170px;
    margin: auto;
    margin-bottom: 30px;
}

.row {
    display: flex;
    flex-wrap: wrap;
}

ul {
    list-style: none;
}

.footer {
    background-color: #242a56;
    padding: 70px 0;
}

.footer-col {
    width: 25%;
    padding: 0 15px;
}

.footer-col h4 {
    font-size: 18px;
    color: #ffffff;
    text-transform: capitalize;
    margin-bottom: 35px;
    font-weight: bold;
    position: relative;
}

.footer-col h4::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -10px;
    
    height: 2px;
    box-sizing: border-box;
    width: 50px;
}

.footer-col ul li:not(:last-child) {
    margin-bottom: 10px;
}

.footer-col ul li a {
    font-size: 16px;
    text-transform: capitalize;
    color: #ffffff;
    text-decoration: none;
    font-weight: 300;

    display: block;
    transition: all 0.3s ease;
}

.footer-col ul li a:hover {
    color: #ffffff;
 
}

.footer-col .social-links a {
    display: inline-block;
    height: 40px;
    width: 40px;
   
    margin: 0 10px 10px 0;
    text-align: center;
    line-height: 40px;
    border-radius: 50%;
    color: #ffffff;
    transition: all 0.5s ease;
}
p{
    color: #ffffff;
}
.bordera-pp{
    background-color: #ffffff;
   
    height: 0.4px;
    width: 100%;
    opacity: 0.2;
    margin-top: 20px;
    
}
.outer-footer{
    padding:10px;
    text-align:left;
    display: flex;
    color:#ffffff;
   margin-bottom: -10px;
}


@media(max-width: 767px) {
    .footer-col {
        width: 50%;
        margin-bottom: 30px;
    }
    .outer-footer {
     
        text-align: center;
       
    }
}

@media(max-width: 574px) {
    .footer-col {
        width: 100%;
    }
    .outer-footer {
    
        text-align: center;
    
    }
}